import { GLOBAL_VARS } from 'utils/constants';
import { documentReady, pageLoad } from 'utils';
import ContactSectionV2 from '../components/ContactSectionV2';
import ContactPopup from '../components/ContactPopup';
import ArticleSection from '../components/ArticleSection';
import PartnersAnim from '../components/animation/PartnersAnim';

export default class PartnersPage {
	constructor() {
		this.init = this.init.bind(this);
	}

	loadFunc() {
		const partnersAnim = new PartnersAnim();
		const articleSection = new ArticleSection().init();
		const contactSection = new ContactSectionV2();
		contactSection.init();
		const popup = new ContactPopup();
		popup.init();
	}

	init() {
		this.loadFunc();
	}
}
