import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { getWindowSize } from '../../utils';
import { GLOBAL_VARS } from '../../utils/constants';
import SplitText from '../../vendors/SplitText';

class PartnersAnim {
	articleAnim() {
		const $article = document.querySelector('.article_section');
		if (!$article) return;

		const $articleTitle = $article.querySelectorAll('.articleTitle');
		const $articleSubtitle = $article.querySelectorAll('.articleSubtitle');
		const $articleIcon = $article.querySelector('.articleIcon');

		const articleTl = gsap.timeline({
			delay: 0.2,
		});

		gsap.set($articleTitle, {
			opacity: 0,
			filter: 'blur(2px)',
		});
		gsap.set($articleSubtitle, {
			opacity: 0,
			x: 40,
		});
		gsap.set($articleIcon, {
			scaleY: 0,
			transformOrigin: 'top',
		});

		articleTl
			.addLabel('start')
			.to($articleTitle, {
				opacity: 1,
				duration: 1,
				ease: 'power1.out',
			}, 'start')
			.to($articleTitle, {
				filter: 'blur(0px)',
				duration: 1.4,
				ease: 'power1.inOut',
			}, 'start')
			.to($articleSubtitle, {
				opacity: 1,
				scale: 1,
				ease: 'Power1.inOut',
				duration: 0.4,
				stagger: 0.6,
			}, 'start+=.86')
			.to($articleSubtitle, {
				x: 0,
				ease: 'Power2.inOut',
				duration: 0.6,
				stagger: 0.6,
			}, 'start+=.86')
			.to($articleIcon, {
				scaleY: 1,
				ease: 'Power1.inOut',
				duration: 0.4,
			})
			.addLabel('end');
	}

	contactAnim() {
		const $contact = document.querySelector('.contact_section_v2');
		const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

		if (!$contact) return;

		const $contactBg = $contact.querySelector('.contactBg');
		const $partnerBgBlur = $contact.querySelector('.partnerBg feGaussianBlur');
		const $partnerBg = $contact.querySelector('.partnerBg');
		const $partnerBgPng = $contact.querySelector('.partnerBgPng');
		const $partnerBgPng2 = $contact.querySelector('.partnerBgPng2');
		const $contactBgOverlay = $contact.querySelector('.contactBgOverlay');
		const $contactTitle = $contact.querySelector('.contactTitle');
		const $contactBtn = $contact.querySelector('.contactBtn');

		const contactTl = gsap.timeline({
			paused: true,
		});

		gsap.set($contactBg, {
			x: '90vw',
		});
		gsap.set($contactBgOverlay, {
			x: '90vw',
		});
		gsap.set($contactTitle, {
			opacity: 0,
			x: 120,
		});
		gsap.set($contactBtn, {
			opacity: 0,
		});
		gsap.set([$partnerBgPng, $partnerBgPng2], {
			opacity: gsap.utils.wrap([1, 0]),
		});

		contactTl
			.addLabel('start')
			.to([$contactBg, $contactBgOverlay], {
				x: 0,
				duration: 0.5,
				ease: 'none',
			}, 'start')
			.addLabel('blurs');
		if (isSafari) {
			gsap.set($partnerBg, {
				opacity: 0,
			});
			contactTl.to([$partnerBgPng, $partnerBgPng2], {
				opacity: gsap.utils.wrap([0, 1]),
				delay: gsap.utils.wrap([0.4, 0]),
				duration: 0.5,
				ease: 'none',
			}, 'blurs');
		} else {
			gsap.set($partnerBgPng, {
				opacity: 0,
			});
			contactTl.to($partnerBgBlur, {
				attr: { stdDeviation: '14 0' },
				duration: 0.5,
			}, 'blurs+=0.1');
		}
		contactTl
			.to($contactTitle, {
				x: 0,
				opacity: 1,
				duration: 0.34,
				ease: 'power1.out',
			}, '-=0.2')
			.to($contactBtn, {
				opacity: 1,
				duration: 0.4,
				ease: 'power1.inOut',
			}, '+=.4');

		const handleEnter = () => {
			contactTl.play();
		};

		ScrollTrigger.create({
			trigger: $contact,
			start: 'center bottom',
			end: 'bottom top',
			onEnter: handleEnter,
		});
	}

	constructor() {
		this.init();
	}

	init() {
		if (getWindowSize().windowWidth < GLOBAL_VARS.mediaPoint1) return;
		gsap.registerPlugin(ScrollTrigger);

		this.articleAnim();
		this.contactAnim();
	}
}

export default PartnersAnim;
